import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    positions: [
      'Bagermester',
      'Driftleder',
      'F&G chef',
      'F&G TK',
      'FC Food',
      'FC NF',
      'Freshfood chef',
      'Produktionskoordinator - Bager',
      'Produktionskoordinator - Slagter',
      'Produktionsleder - Bager',
      'SVC',
      'TK Bageri',
      'TK Drift',
      'TK Food',
      'TK NF',
      'TK Service',
      'TL Food',
      'TL NF',
      'TL service'
    ],
    colors: [
      '>12 MDR',
      '6 MDR',
      'Klar'
    ],

    desserts: [
      {
        shop: '101',
        name: 'a',
        currentPosition: 'TL service',
        nextPosition: 'Driftleder',
        color: 'Klar',
      },
      {
        shop: '101',
        name: 'b',
        currentPosition: 'TK NF',
        nextPosition: 'TK Food',
        color: '6 MDR',
      },
      {
        shop: '101',
        name: 'c',
        currentPosition: 'TL Food',
        nextPosition: 'Driftleder',
        color: '>12 MDR',
      },
      {
        shop: '102',
        name: 'd',
        currentPosition: 'Produktionskoordinator - Bager',
        nextPosition: 'Bagermester',
        color: '6 MDR',
      },
      {
        shop: '102',
        name: 'e',
        currentPosition: 'Bagermester',
        nextPosition: 'Driftleder',
        color: '6 MDR',
      },
      {
        shop: '102',
        name: 'f',
        currentPosition: 'SVC',
        nextPosition: 'Driftleder',
        color: 'Klar',
      },
      {
        shop: '102',
        name: 'g',
        currentPosition: 'TL NF',
        nextPosition: 'FC NF',
        color: 'Klar',
      },
    ]
  },
  getters: {
    allDesserts: state => {
      return state.desserts;
    },
    positions : state => {
      return state.positions;
    },
    colors : state => {
      return state.colors;
    },
    chartData: state => {
      let result = [
          {
           label: 'Klar',
           backgroundColor: 'blue',
           data: []
         },
         {
           label: '6 MDR',
           backgroundColor: 'green',
           data: []
         },
         {
          label: '>12 MDR',
          backgroundColor: 'yellow',
          data: []
        }
      ];

      for (let i = 0; i < state.positions.length; i++) {     
        let position = state.positions[i];
        result[0].data.push(state.desserts.filter(a => a.nextPosition === position && a.color === 'Klar').length);
        result[1].data.push(state.desserts.filter(a => a.nextPosition === position && a.color === '6 MDR').length)
        result[2].data.push(state.desserts.filter(a => a.nextPosition === position && a.color === '>12 MDR').length)
        
      }
      return result;
    }
  },
  mutations: {
    edit (state, payload) {
      Object.assign(state.desserts[payload.index], payload.item)
    },
    add (state, item) {
      state.desserts.push(item)
    },
    delete (state, index) {
      state.desserts.splice(index, 1)
    }

  },
  actions: {
  },
  modules: {
  }
})
