      LineChart
    },
    data () {
      return {
        
        options: {
            scales: {
                xAxes: [{
                    stacked: true
                }],
                yAxes: [{
                    stacked: true,
                    ticks: {
                
                    stepSize: 1
                }
                }]
        }
        }
      }
    },
    mounted () {
      this.fillData()
    },
    computed: {
      ...mapGetters([
      'allDesserts',
      'positions',
      'colors',
      'chartData'
    ]),
    datacollection () {
        return this.datacollection = {
          labels: this.positions,
          datasets: this.chartData
        }
      },
    },
