<template>
  <v-data-table
    :headers="headers"
    :items="allDesserts"
    sort-by="name"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar flat color="white">
        <v-toolbar-title>Pipeline</v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              color="primary"
              dark
              class="mb-2"
              v-bind="attrs"
              v-on="on"
            >Ny</v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.shop" label="Butik"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.name" label="Navn"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <!-- <v-text-field v-model="editedItem.fat" label="Nuværende stilling"></v-text-field> -->
                    <v-select
                      v-model="editedItem.currentPosition"
                      :items="positions"
                      label="Nuværende stilling"
                      required
                    ></v-select>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <!-- <v-text-field v-model="editedItem.carbs" label="Næste stilling"></v-text-field> -->
                    <v-select
                      v-model="editedItem.nextPosition"
                      :items="positions"
                      label="Næste stilling"
                      required
                    ></v-select>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <!-- <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field> -->
                    <v-select
                      v-model="editedItem.color"
                      :items="colors"
                      label="Farve"
                      required
                    ></v-select>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="save">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
        small
        @click="deleteItem(item)"
      >
        mdi-delete
      </v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="initialize">Reset</v-btn>
    </template>
  </v-data-table>
</template>

<script>
  import { mapGetters, mapMutations } from 'vuex'
  export default {
    data: () => ({
      dialog: false,
      headers: [
        {
          text: 'Butik',
          value: 'shop',
        },
        { text: 'Navn', value: 'name' },
        { text: 'Nuværende stilling', value: 'currentPosition' },
        { text: 'Næste stilling', value: 'nextPosition' },
        { text: 'Farve', value: 'color' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        shop: '',
        name: '',
        currentPosition: '',
        nextPosition: '',
        color: '',
      },
      defaultItem: {
        shop: '',
        name: '',
        currentPosition: '',
        nextPosition: '',
        color: '',
      },
    }),
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
      ...mapGetters([
      'allDesserts',
      'positions',
      'colors'
    ])
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
    },
    created () {
      this.initialize()
    },
    methods: {
      initialize () {
        this.desserts = this.allDesserts
      },
      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },
      deleteItem (item) {
        const index = this.desserts.indexOf(item)
        confirm('Are you sure you want to delete this item?') && this.delete(index)
      },
      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },
      save () {
        if (this.editedIndex > -1) {
          this.edit({index: this.editedIndex, item: this.editedItem})
          // Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else {
          // this.desserts.push(this.editedItem)
          this.add(this.editedItem)
        }
        this.close()
      },
      ...mapMutations([
      'add', // map `this.increment()` to `this.$store.commit('increment')`

      // `mapMutations` also supports payloads:
      'edit',
      'delete'
    ]),
    },
  }
</script>